p{
    margin: 0px;
}
.section4{
    position: relative;
    width: 81.25%;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-wrap: wrap;
    row-gap: 30px;
}
.section4-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #101010;
    width: 100%;
}
.section4-span{
    color: #FF56B3;
}
.section4-grid-container{
    display: grid;
    grid-template-columns: 30% 30% 30%;
    column-gap: 42px;
    row-gap: 30px;

}
.section4-item{
    max-width: 370px;
    height: 335px;
    border: 1px solid #FF56B3;
    border-radius: 10px;
    
}
.section4-item-title{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFF6FB;
    border-radius: 9px 9px 0px 0px;
    width: 100%;
    height: 105px;
    padding: 25px 25px 0px 25px;
}
.section4-item-title>p{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    text-transform: uppercase;
    color: #202020;
}
.section4-item-text{
    position: relative;
    top: 21px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #101010;
    text-align: center;
    padding: 0px 35px 0px 35px;
}
@media(max-width: 1250px){
    .section4{
        top: 100px;
    }
    .section4-grid-container{
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 42px;
        row-gap: 30px;
    
    }
}
@media(max-width: 810px){
    .section4-grid-container{
        display: grid;
        grid-template-columns: 1fr;
        column-gap: 42px;
        row-gap: 30px;
    }
    .section4-item-title{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 800;
        font-size: 18px;
        line-height: 25px;
        text-align: center;
        text-transform: uppercase;
        color: #202020;
    }
    .section4-item-text{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
    }
}
@media(max-width: 580px){
    .section4{
        top: 463px;
    }
}