.section3{
    position: relative;
    width: 81.25%;
    height: fit-content;
    padding: 60px 60px 60px 60px;
    background: #FFF6FB;
    border-radius: 10px;   
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.section3-title{
    min-width: 100%;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #FF56B3;
}
.img{
    min-width: 70px;
    max-width: 70px;
    min-height: 70px;
    max-height: 70px;
    background: #FF56B3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Futura Md BT';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    color: #FFFFFF;
    z-index: 2;
}

.short-line{
    position: absolute;
    left: 34px;
    top: 30px;
}
.long-line{
    width: 2px;
    height: 287px;
    position: relative;
    left: 0px;
    top: -55px;
    left: 34px;
}
.long-line2{
    position: absolute;
    left: -495px;
    top: 0px;
    opacity: 0;
}


.section3-containers{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    column-gap: 50px;
    position: relative;
    top: 40px;
    height: fit-content;
}
.section3-container1{
    min-width: 1fr;
    height: fit-content;
    row-gap: 30px;
    display: flex;
    flex-direction: column;


}
.section3-container2{
    min-width: 1fr;
    height: 500px;
    row-gap: 30px;
    display: flex;
    flex-direction: column;

}
.a1-title, .a2-title, .a3-title, .a4-title, .a5-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 33px;
    text-transform: uppercase;
    color: #101010;
}
.section3-a1{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    column-gap: 32px;
    align-items: start;
    
}
.section3-a2{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    column-gap: 32px;
    align-items: start;
}
.section3-a3{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    column-gap: 32px;
    align-items: start;
    
}
.section3-a4{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    column-gap: 32px;
    align-items: start;
    position: relative;
    top: -291px;
}
.section3-a5{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    column-gap: 32px;
    align-items: start;
    position: relative;
    top: -291px;
}
.a1-p, .a2-p, .a3-p, .a4-p, .a5-p{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #101010;
} 
@media(max-width: 1235px){
    
    .section3-containers{
        flex-wrap: wrap;
        row-gap: 30px;
    }
    .long-line2{
        left: 35px;
        opacity: 1;
    }
    .long-line{
        opacity: 1;
    }
    .short-line{    
        opacity: 0;
    }
    .section3-a5{
        height: 89px;
    }
}
