:root{
    --display: none;
    --display2: none;
    --display3: none;
}
.section6-container{
        position: relative;
        top: 180px;
        width: 81.25%;
        min-height: 475px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: center;
        column-gap: 39px;
        z-index: 5;
}
.section6-left{
    width: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 11px;
    position: relative;
    top:-21px;
}
.left-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #FF56B3;

}
.left-text1, .left-text2, .left-text3{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #101010;
    position: relative;
    left: 33px;
}
.left-text1::before, .left-text2::before, .left-text3::before{
    content: "";
    width: 19px;
    height: 19px;
    background-color: #FF56B3;
    display: block;
    position: relative;
    top: 23px;
    left: -33px;
    border-radius: 50%;
    
}
.section6-right{
    background: #FBFBFB;
    border-radius: 10px;
    width: 50%;
    height: 475px;  
    padding: 65px 96px 0px 61px;
    display: flex;
    row-gap: 60px;
    flex-direction: column;
}
.right-title{
    display: flex;
    flex-wrap: nowrap;
    column-gap: 17px;
}
.right-title>img{
    width: 53.14px;
    height: 50px;
}
.right-title-text{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 33px;
    display: flex;
    align-items: center;
    text-transform: capitalize;
    color: #FF56B3;
}
.form{
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 24px;
}
.input1, .input2, .input3{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    display: flex;
    align-items: center;
    color: #000000;
    border-right: none;
    border-left: none;
    border-top: none;
    border-bottom-style: solid;
    background-color: #FBFBFB;
    width: 100%;
}
.div::before{
    
    content: '';
    width: 19px;
    height: 19px;
    background-color: #FF56B3;
    display: var(--display);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    left: 0px;
    top: 45px;
    border-radius: 50%;
}
.div::after{
    
    content: 'Please complete the required field';
    width: max-content;
    height: 19px;
    display: var(--display);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    color: red;
    left: 40px;
    top: 0px;
}
.div{
    min-width: 75%;
}
.div2::before{
    
    content: '';
    width: 19px;
    height: 19px;
    background-color: #FF56B3;
    display: var(--display2);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    left: 0px;
    top: 45px;
    border-radius: 50%;
}
.div2::after{
    
    content: 'Please complete the required field';
    width: max-content;
    height: 19px;
    display: var(--display2);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    color: red;
    left: 40px;
    top: 0px;
}
.div2{
    min-width: 75%;
}
.div3::before{
    
    content: '';
    width: 19px;
    height: 19px;
    background-color: #FF56B3;
    display: var(--display3);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    left: 0px;
    top: 45px;
    border-radius: 50%;
}
.div3::after{
    
    content: 'Please complete the required field';
    width: max-content;
    height: 19px;
    display: var(--display3);
    justify-content: space-between;
    flex-direction: row;
    position: relative;
    color: red;
    left: 40px;
    top: 0px;
}
.div3{
    min-width: 75%;
}
.submit{
    width: 75%;
    height: 55px;
    background: #FF56B3;
    border-radius: 100px;
    border: none;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
    position: relative;
    top: 26px;
    }
    input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}
@media(max-width:1250px){
    .section6-container{
        position: relative;
        top: 360px;
    }
}
@media(max-width:1068px){
    .section6-container{
        display: flex;
        flex-wrap: wrap;
        top: 600px;
    }

}
@media(max-width: 820px){
    .submit{
        font-size: 12px;
        width: 100%;
    }
}
@media(max-width:768px){
    .section6-right{
        display: flex;
        flex-wrap: wrap;
        padding: 40px;
        width: 100%;
    }
    .section6-left{
        width: 100%;
    }
    .right-title-text
        {font-size: 24px;
        line-height: 110%;}
    .div, .div2, .div3{
        width: 75%;
    }
    .input1, .input2, .input3{
        width: 100%;
    }

}