.header{
    position: relative;
    top: 16px;
    width: 100%;
    height: 99px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header-container{
    width: 81.25%;
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    align-items: center;
    column-gap: 8.632%;
}
.header-logo{
    width: 228px;
    height: 99px;   
}
.header-items{
    position: relative;
    left: -3px;
    display: flex;
    flex-direction: row;
    column-gap: 29px;
    align-items: center;
}
.header-item1{
    width: min-content;
    height: 14px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: capitalize;
    color: #101010;
}
.header-item2{
    width: max-content;
    height: 14px;
    font-family: 'Open Sans';
