.header-item1:hover,.header-item2:hover,.header-item3:hover,.header-item4:hover,.header-item5:hover,.header-item6:hover{
    color: #FF56B3;
    transition-duration: 0.2s;
}
.header-button{
    width: max-content;
    position: relative;
    left: 13px;
    width: 215px;
    height: 40px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF; 
    background: #252525;
    border-radius: 80px;
    cursor: pointer;
}
@media(max-width: 1115px){
    .header-items{
        column-gap: 10px;
    }
}
@media(max-width: 1035px){
    .header-item1, .header-item2, .header-item3, .header-item4, .header-item5, .header-item6{
        display: none;
    }
    .header-container{
        justify-content: space-between;
    }
}