.section1{
    position: relative;
    
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: max-content;
}
.section1-container{
    width: 81.25%;
    height: max-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #303030;
    border-radius: 10px;
    padding: 17px 38px 0px 86px;
    background-image: url('../images/section1-shadow.svg'), url('../images/triangle1.svg'), url('../images/triangle2.svg'), url('../images/triangle3.svg');
    background-repeat: no-repeat;
    background-position: right bottom, left, 680px, right top ;
}
.section1-text{
    max-width: 705px;
    height: 165px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 55px;
    text-transform: uppercase;
    color: #FFFFFF;
}
.section1-span{
    color: #FF56B3;
}
.section1-circle{
    background-image: url('../images/section1-logo.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-position-y:bottom;
}
@media(max-width:1168px){
    .section1-container{
        flex-wrap: wrap;
        justify-content: center;
        background-image: url('../images/section1-shadow.svg'), url('../images/triangle1.svg'), url('../images/triangle2.svg'), url('../images/triangle3.svg');
        background-repeat: no-repeat;
        background-position: 50px 180px, left, 680px, right top ;
    }
    .section1-circle{
        position: relative;
        
    }
}
@media(max-width: 880px){
    .section1-text{
        font-size: 40px;
    }
}
@media(max-width: 768px){
    .section1-container{
        width: 87.24%;
        height: max-content;
        padding: 0px 24px 0px 70px;
    }
    .section1-circle{
        position: relative;
    }
    .section1-text{
        width: max-content;
    }
    
}
@media(max-width: 620px){
    .section1-text{
        font-size: 30px;
    }
}
@media(max-width: 568px){
    
    .section1-container{
        width: 87.24%;
        height: 402px;
        padding: 0px 24px 0px 30px;
    }
    .section1-circle{
        position: relative;
        width: 249px;
        height: 250px;
        width: 220px;
        height: 210px;
        background-size:195px 195px;
        background-position-y: 15px;
        
    }
    .section1-text{
        width: max-content;
        font-size: 30px;
    }
}
@media(max-width: 450px){
    .section1-container{
        padding: 0px;
    }
    .section1-text{
        font-size: 25px;
    }
}