.section5{
    position: relative;
    top: 80px;
    width: 81.25%;
    min-height: 350px;
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
}
.section5-title{
    width: 100%;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #FF56B3;
}
.section5-items{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    row-gap: 40px;
    position: relative;
    top: 30px;
    
}
.section5-container{
    display: flex;
    justify-content: space-between;
    align-items: start;
    background: #303030;
    border-radius: 10px;
    padding: 17px 38px 0px 86px;
    background-image:url('../images/triangle1.svg'), url('../images/triangle2.svg'), url('../images/triangle3.svg');
    background-repeat: no-repeat;
    background-position: left, 680px, right top ;
    width: 100%;
    max-height: 370px;
    padding-top: 72px;
}
.container-texts{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    margin: 0px;
}
.container-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 45px;
    line-height: 55px;
    text-transform: uppercase;
    color: #FFFFFF;
    margin: 0px;

}
.container-text{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: #FFFFFF;
    margin: 0px;

}
.container-image{
    border-radius: 6px;
    position: relative;
    top: -12px;
    width: 54%;

}
@media(max-width: 1250px){
    .section5{
        top: 180px;
    }
}
@media(max-width: 1068px){
    .section5-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: start;
        row-gap: 30px;
        padding: 70px 70px 0px 70px;
    }
    .container-image{
        width: 77%;
    
    }
}
@media(max-width: 768px){
    .container-image{
        width: 88%;
    
    }
    .container-text{
        font-weight: 600;
        font-size: 25px;
        line-height: 130%;
    }
    .container-title{
        font-weight: 800;
        font-size: 24px;
        line-height: 35px;
    }
}
@media(max-width: 580px){
    .section5{
        top: 500px;
    }
}
