
.section2{
    position: relative;
    width: 81.25%;
    display: grid;
    grid-template-columns: 1fr 401px;
    column-gap: 70px;
    height: max-content;
}
.section2-left-container{

}
.left-container-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #101010;
}
.left-container-span{
    color: #FF56B3;
}
.left-container-p{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: #101010;
}
.section2-right-container{
    display: flex;
    row-gap: 42px;
    flex-wrap: wrap;
    width: 401px;
    height: 441px;
    background: #FFE5F3;
    border-radius: 10px;
    padding: 45px 68px 47px 55px;
}
.right-container-top{
    display: flex;
    flex-wrap: wrap;
    row-gap: 11px;
}
.top-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #FF56B3;
}
.top-text{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 155%;
    color: #101010;
}
.bottom-title{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    text-transform: uppercase;
    color: #101010;
}
.bottom-text{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 155%;  
    color: #101010;
}
@media(max-width:1250px){
    .section2{
        position: relative;
        top: 60px;
    }
}
@media(max-width:1035px){
    .section2{
        display: flex;
        flex-wrap: wrap;
        row-gap: 60px;
    }
    .section2-left-container{
        width: 100%;
    }
    .section2-right-container{
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .right-container-top{
        justify-content: center;
    }
    .right-container-bottom{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}
