.footer{
    width: 100%;
    display: flex;
    justify-content: center;
    background: #303030;
    background-image:url('../images/triangle1.svg'), url('../images/triangle2.svg'), url('../images/triangle3.svg');
    background-repeat: no-repeat;
    background-position: left, 680px, right top ;
    width: 100%;
    height: 352px;
    position: relative;
    top: 280px;
    padding-top: 35px;
    padding-bottom: 35px;    
}
.footer-container{
